.shortener-form.shortener-form {
  margin: 0 auto; }
  .shortener-form.shortener-form.ant-form-inline .ant-form-item {
    margin-right: 0; }
  .shortener-form.shortener-form .url-input-form-item input {
    border-color: #1890ff; }
  .shortener-form.shortener-form .shortener-form-button {
    text-shadow: none;
    box-shadow: none; }
  @media (min-width: 341px) {
    .shortener-form.shortener-form .url-input-form-item input {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border-right: none; }
    .shortener-form.shortener-form .shortener-form-button {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; } }
  @media (min-width: 572px) {
    .shortener-form.shortener-form .url-input-form-item .ant-form-item-control-wrapper {
      width: 420px; }
    .shortener-form.shortener-form .shortener-button-form-item .ant-form-item-control-wrapper {
      width: 120px; } }

.urls {
  max-width: 540px;
  margin: 16px auto 0;
  text-align: left;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  background-color: #fff;
  padding: 24px; }
  .urls .url {
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.2); }
    .urls .url:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none; }
  .urls .long-url {
    display: block;
    font-size: 16px;
    line-height: 32px;
    font-weight: 300;
    margin-bottom: 4px; }
  .urls .short-url {
    display: block;
    font-size: 13px;
    font-weight: 700; }
    .urls .short-url .display {
      margin-right: 8px; }
    .urls .short-url button {
      text-transform: uppercase;
      font-size: 10px; }
      .urls .short-url button.ant-btn > .anticon + span {
        margin-left: 5px;
        margin-right: 3px; }
